<template>
<!--  -->
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/actives" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>活动管理</p>
    </header>
    <!-- 工会信息 -->
    <main v-for="(item,index) in list" :key="index">
      <div class="chairman">
        <div class="list" v-for="(s,index) in item.lists" :key="index">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
        </div>
        <div style="width:100%; background-color:white;" v-for="(m,index) in item.img" :key="'info2-'+index">
          <div class="fl-ex al-it ju-be">
            <p>{{m.p}}</p>
            <span>{{m.span}}</span>
          </div>
          <a>{{m.wenzi}}</a>
          <div class="fl-ex al-it ju-be">
            <p>{{m.p1}}</p>
            <span>{{m.span1}}</span>
          </div>
          <img :src="m.pic" />
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          lists: [
            {
              p: "活动主题",
              span: "助老助残"
            },
            {
              p: "发布机构",
              span: "雨滴筹"
            },
            {
              p: "所需人数",
              span: "20"
            },
            {
              p: "已报名人数",
              span: "15"
            },
            {
              p: "活动日期",
              span: "2019-03-25"
            },
            {
              p: "报名截止日期",
              span: "2019-03-15"
            },
            {
              p: "活动联系人",
              span: "张萌"
            },
            {
              p: "活动联系电话",
              span: "15897230294"
            },
            {
              p: "活动地点",
              span: "西宁市虎台路61号"
            }

          ],
          img: [
            {
              p: "活动简介",
              span: "",
              p1: "活动海报",
              span1: "",
              wenzi:'随着志愿者活动的持续展开，我们已经走出校园，服务范围已经扩展到更多的地方。多次志愿活动的成功举办，让我们的志愿者信息倍增，激情澎湃我们将团结更多的志愿者团体，尽我们最大的努力，在助老助残日来临之际，我们希望能为更多社区的孤寡老人，残疾人带去一片爱心，一缕阳光，一份温暖，让他们在晚年之际，仍然能感受到社会对他们的关心与关注。也希望能为这个地区的和谐与稳定贡献我们的绵薄之力！',
              pic: "../../../static/img/47.png"
            },
          ]
        },
      ]
    };
  }
};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left: 32%;
}
/* 工会信息  */
main {
  width: 100%;
}
main .chairman {
  width: 100%;
  background-color: #e2e2e2;
}
main .chairman img {
  width: 30%;
  height: 10rem;
  margin-left: 3%;
  margin-top: 5%;
  padding-bottom: 3%;
}
main .chairman .list {
  width: 100%;
  height: 2.5rem;
  background-color: white;
}
main .chairman:last-child {
  padding-bottom: 0.5rem;
}
main .chairman > div > div {
  width: 94%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e2e2e2;
}
main .chairman > div > div p {
  font-size: 0.8rem;
  color: #8c8c8c;
}
main .chairman > div > div span {
  font-size: 0.8rem;
  color: #393939;
}
main a {
  font-size: 0.87rem;
  line-height: 1.3rem;
  text-indent: 2rem;
  letter-spacing: 0.05rem;
  text-align: left;
  width: 95%;
  margin: 0 auto;
  color: #313131;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
</style>


